.oxford {
  @include sectionMb(70px);
  @include bg-cover;

  padding-top: 153px;
  padding-bottom: 149px;
  background-image: url('../img/oxford-fon.png');

  &__container {
    padding-left: 36px;

    @include mobile {
      padding-left: 12px;
    }
  }

  &__content {
    margin-bottom: 49px;
    max-width: 508px;

    @include mobile {
      margin-bottom: 0;
      max-width: 187px;
    }
  }

  &__title {
    margin: 0;
    font-family: var(--second-family);
    font-weight: 900;
    font-size: 60px;
    line-height: 120%;
    color: var(--gold-color);

    @include mobile {
      margin-bottom: 3px;
      font-size: 24px;
    }
  }

  &__descr {
    margin: 0;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 48px;
    line-height: 120%;
    color: var(--white-color);

    @include mobile {
      font-size: 16px;
    }
  }

  &__btn {
    padding: 21px 30px;

    @include mobile {
      position: absolute;
      top: 242px;
      width: 296px;
    }
  }

  @include tablet {
    background-image: url('../img/oxford-fon-tablet.png');
  }

  @include small-tablet {
    background-image: url('../img/oxford-fon-small-tablet.png');
  }

  @include mobile {
    position: relative;
    margin-bottom: 162px;
    padding: 30px 0;
    min-height: 210px;
    background-image: url('../img/oxford-fon-mobile.png');
  }
}
