.hero {
  @include sectionMb(70px);
  @include bg-cover;

  padding: 80px 0;
  background-image: url('../img/hero-fon.png');

  &__title {
    margin: 0;
    margin-bottom: 50px;
    max-width: 496px;
    font-weight: 700;
    font-size: 48px;
    line-height: normal;
    color: var(--grey-color);

    @include mobile {
      font-size: 20px;
    }
  }

  &__btn {
    @include mobile {
      position: absolute;
      top: 242px;
      width: 296px;
      transform: translateX(-18px);
    }
  }

  @include tablet {
    background-image: url('../img/hero-fon-tablet.png');
  }

  @include small-tablet {
    background-image: url('../img/hero-fon-small-tablet.png');
  }

  @include mobile {
    @include sectionMb(162px);

    position: relative;
    padding: 30px 0;
    padding-left: 18px;
    min-height: 210px;
    background-image: url('../img/hero-fon-mobile.png');
  }
}
