.header {
  position: relative;
  padding-bottom: 32px;
  background: linear-gradient(var(--grey_light-color) 50px, var(--white-color) 50px 100%);

  @include tablet {
    background: linear-gradient(
      var(--white-color) 58px,
      var(--grey_light-color) 58px 108px,
      var(--white-color) 108px 100%
    );
  }

  @include big-mobile {
    background: linear-gradient(var(--grey_light-color) 50px, var(--white-color) 50px 100%);
  }

  &__create-line {
    display: none;

    @include mobile {
      position: absolute;
      display: inline-block;
      width: 100%;

      &::before {
        content: '';
        position: absolute;
        top: 66px;
        width: 100%;
        height: 1px;
        background-color: var(--grey_light-color);
      }

      &::after {
        content: '';
        position: absolute;
        top: 115px;
        width: 100%;
        height: 1px;
        background-color: var(--grey_light-color);
      }
    }
  }

  &__container {
    grid-template-rows: 50px 91px 60px;
    padding-right: 10px;

    @include tablet {
      grid-template-rows: 58px 50px 92px 60px;
      padding-right: 36px;
    }

    @include less-tablet {
      grid-template-rows: 56px 50px 63px 83px 62px;
    }

    @include small-tablet {
      padding-right: 72px;
    }

    @include big-mobile {
      grid-template-rows: 52px 16px 45px 53px 62px;
      padding-right: 12px;
    }
  }

  &__phone {
    @include flex-v-center;

    grid-column: 5 span;
    align-self: center;
    width: 145px;
    font-weight: 400;
    font-size: 14px;
    line-height: 100%;
    color: var(--secondary-color);
    transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;

    &--svg {
      width: 22px;
      height: 22px;
      fill: currentColor;
    }

    &:focus:not(:hover):not(:active) {
      outline: none;
      background-color: var(--bone-color);
    }

    &:hover {
      outline: none;
      color: var(--secondary_light-color);
    }

    &:active {
      color: var(--secondary_shade-color);
    }

    @include small-desktop {
      grid-column: 2 span;
    }

    @include tablet {
      grid-column: 9 span;
    }

    @include less-tablet {
      grid-column: 6 span;
    }

    @include mobile {
      grid-column: 3 span;
      grid-row: 3;
    }
  }

  &__wrap {
    @include flex-all-end;

    grid-column: 2 span;
    grid-row: 3;
    column-gap: 25px;

    &-user {
      @include flex-all-center;

      padding: 3px 4px;
      width: 34px;
      height: 36px;
      transition: background-color 0.3s ease-in-out;

      &--svg {
        width: 26px;
        height: 28px;
        fill: var(--primary-color);
        transition: fill 0.3s ease-in-out;
      }

      &:focus-visible:not(:hover):not(:active) {
        outline: none;
        background-color: var(--primary_light-color);
      }

      &:hover {
        outline: none;
      }

      &:active {
        outline: none;
      }

      &:focus-visible:not(:hover):not(:active) .header__wrap-user--svg {
        fill: var(--white-color);
      }

      &:hover .header__wrap-user--svg {
        fill: var(--primary_light-color);
      }

      &:active .header__wrap-user--svg {
        fill: var(--primary_shade-color);
      }
    }

    &-basket {
      @include flex-v-center;

      padding: 3px 4px;
      width: 49px;
      height: 34px;
      transition: background-color 0.3s ease-in-out;

      &--quantity {
        @include flex-all-center;

        border-radius: 100%;
        width: 14px;
        height: 15px;
        font-weight: 700;
        font-size: 10px;
        line-height: 100%;
        color: var(--white-color);
        background-color: var(--secondary-color);
      }

      &--svg {
        width: 30px;
        height: 28px;
        fill: var(--primary-color);
        transition: fill 0.3s ease-in-out;
      }

      &:focus-visible:not(:hover):not(:active) {
        outline: none;
        background-color: var(--primary_light-color);
      }

      &:hover {
        outline: none;
      }

      &:active {
        outline: none;
      }

      &:focus-visible:not(:hover):not(:active) .header__wrap-basket--svg {
        fill: var(--white-color);
      }

      &:hover .header__wrap-basket--svg {
        fill: var(--primary_light-color);
      }

      &:active .header__wrap-basket--svg {
        fill: var(--primary_shade-color);
      }
    }

    @include tablet {
      grid-column: 8/2 span;
      grid-row: 2;
      transform: translate(5px);
    }

    @include less-tablet {
      grid-column: 2 span;
      grid-row: 3;
      align-items: end;
    }

    @include mobile {
      grid-column: 3 span;
      grid-row: 3;
      align-items: center;
    }
  }
}

.logo {
  grid-column: 2 span;
  grid-row: 2;
  align-self: center;

  @include tablet {
    grid-column: 2 span;
    grid-row: 3;
  }

  @include less-tablet {
    grid-column: 4 span;
    align-self: end;
  }

  @include mobile {
    grid-column: 6 span;
    grid-row: 1;
    justify-self: center;
    align-self: center;
  }
}
