.feedback {
  @include sectionMb(70px);

  @include small-tablet {
    @include sectionMb(69px);
  }

  &__left {
    grid-column: 6 span;

    @include tablet {
      grid-column: 4 span;
    }

    @include less-tablet {
      grid-column: 3 span;
    }

    @include small-tablet {
      grid-column: 4 span;
    }

    @include mobile {
      grid-column: 6 span;
    }
  }

  &__title {
    margin-bottom: 9px;
  }

  &__desc {
    margin: 0;
    margin-bottom: 16px;
    font-weight: 400;
    font-size: 14px;
    line-height: normal;
    color: var(--grey_shade-color);
  }

  &__right {
    grid-column: 6 span;
    background-image: url('../img/feedback-fon.png');
    background-position: left center;
    background-size: contain;
    background-repeat: no-repeat;

    @include tablet {
      grid-column: 5 span;
    }

    @include less-tablet {
      grid-column: 3 span;
    }

    @include small-tablet {
      grid-column: 2 span;
      background-image: url('../img/feedback-fon-small-tablet.png');
    }

    @include mobile {
      display: none;
    }
  }
}
