.useful {
  @include sectionMb(70px);

  &__list {
    @include mobile {
      row-gap: 24px;
    }
  }

  &__item {
    grid-column: 6 span;

    @include tablet {
      grid-column: 3 span;
    }

    @include mobile {
      grid-column: 6 span;
    }

    &-tablet {
      display: none;

      @include tablet {
        display: block;
      }

      @include less-tablet {
        display: none;
      }

      @include mobile {
        display: block;
      }
    }

    &-mobile {
      display: none;

      @include mobile {
        display: block;
      }
    }
  }
}
