.categories {
  @include sectionMb(70px);

  &__list {
    gap: 32px;

    @include mobile {
      column-gap: 16px;
    }
  }

  &__item {
    grid-column: 4 span;

    @include tablet {
      grid-column: 3 span;
    }

    &--top {
      grid-column: 6 span;
    }

    &--tablet {
      @include tablet {
        grid-column: 4 / span 3;
        grid-row: 2;
      }
    }
  }
}
