.nav {
  &-top {
    grid-column: 7 span;
    justify-self: right;
    align-self: center;

    @include small-desktop {
      grid-column: 10 span;
    }

    @include tablet {
      grid-column: 7 span;
      grid-row: 2;
      justify-self: start;
      transform: translate(-4px);
    }

    @include less-tablet {
      width: 101%;
      grid-column: 6 span;
    }

    @include big-mobile {
      display: none;
    }

    &__list {
      display: flex;
      column-gap: 54px;

      @include less-tablet {
        justify-content: space-between;
        column-gap: 0;
      }
    }

    &__link {
      padding: 2px 5px;
      font-size: 14px;
      font-weight: 400;
      line-height: 100%;
      color: var(--grey-color);
      transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;

      &:focus:not(:hover):not(:active) {
        outline: none;
        background-color: var(--pink_violet-color);
      }

      &:hover {
        outline: none;
        color: var(--primary-color);
      }

      &:active {
        color: var(--primary_shade-color);
      }
    }
  }

  &-main {
    grid-column: 10 span;
    grid-row: 2;
    justify-self: right;
    align-self: center;

    &__list {
      display: flex;
      column-gap: 82px;

      @include small-desktop {
        column-gap: 41px;
      }

      @include tablet {
        column-gap: 32px;
      }

      @include less-tablet {
        justify-content: space-between;
        column-gap: 0;
      }

      @include mobile {
        justify-content: start;
        column-gap: 22px;
      }
    }

    &__link {
      padding: 2px 5px;
      font-weight: 400;
      font-size: 16px;
      line-height: normal;
      color: var(--black-color);
      transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;

      &:focus:not(:hover):not(:active) {
        outline: none;
        background-color: var(--pink_violet-color);
      }

      &:hover {
        outline: none;
        color: var(--primary-color);
      }

      &:active {
        color: var(--primary_shade-color);
      }

      @include less-tablet {
        font-size: 14px;
      }
    }

    @include tablet {
      grid-column: 3/7 span;
      grid-row: 3;
      transform: translate(4px);
    }

    @include less-tablet {
      grid-column: 6 span;
      grid-row: 4;
      justify-self: start;
      width: 103%;
      transform: translate(-8px);
    }

    @include mobile {
      width: 203%;
    }
  }
}
