.specials {
  @include sectionMb(70px);

  &__list {
    @include mobile {
      row-gap: 24px;
    }
  }

  &__item {
    grid-column: 3 span;

    &--center {
      grid-column: 6 span;

      @include tablet {
        grid-column: 3 span;
      }
    }

    &-3 {
      @include less-tablet {
        display: none;
      }

      @include mobile {
        display: block;
      }
    }

    &-mobile {
      display: none;

      @include mobile {
        display: block;
      }
    }

    @include mobile {
      grid-column: 6 span;
    }
  }

  @include mobile {
    @include sectionMb(69px);
  }
}
